.promo {
  .list-reset();
  display: grid;
  background-color: #ffffff;

  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(3, 256px);
    grid-column: 1 / -1;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 400px);
    grid-column: 1 / -1;
  }
}

.promo__item {
  @media (max-width: @mobile-width) {
    text-align: left;
    padding: 10px @base-indent 20px 64px;

    &:first-child {
      margin-top: 25px;
    }

    &:last-child {
      margin-bottom: 25px;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 154px 33px 64px 33px;
  }

  @media (min-width: @desktop-width) {
    padding: 170px 50px 70px 50px;
  }

  &--house {
    background-image: url("../img/svg/icon-house.svg");
    background-repeat: no-repeat;
    background-position: @base-indent 2px;

    @media (min-width: @tablet-width) {
      background-position: center 60px;
      background-size: 77px 72px;
    }

    @media (min-width: @desktop-width) {
      background-position: center 70px;
    }
  }

  &--food {
    background-image: url("../img/svg/icon-food.svg");
    background-repeat: no-repeat;
    background-position: @base-indent 2px;

    @media (min-width: @tablet-width) {
      background-position: center 60px;
      background-size: 80px 68px;
    }

    @media (min-width: @desktop-width) {
      background-position: center 70px;
    }
  }

  &--souvenirs {
    background-image: url("../img/svg/icon-souvenirs.svg");
    background-repeat: no-repeat;
    background-position: 22px 2px;

    @media (min-width: @tablet-width) {
      background-position: center 60px;
      background-size: 67px 75px;
    }

    @media (min-width: @desktop-width) {
      background-position: center 70px;
    }
  }
}

.promo__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  padding: 0;
  margin: 0 0 18px 0;

  @media (min-width: @tablet-width) {
    font-size: 21px;
    margin: 0 0 22px 0;
  }
}

.promo__text {
  text-transform: uppercase;
  color: @base-text-color;
  padding: 0;
  margin: 0;
}
