.video {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.video__photo-poster {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.video__button {
  width: 48px;
  height: 54px;
  color: rgba(255, 255, 255, 0.7);
  background-color: transparent;
  position: absolute;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 280px;
    height: 177px;

    @media (min-width: @tablet-width) {
      width: 648px;
      height: 500px;
    }

    @media (min-width: @desktop-width) {
      width: 800px;
      height: 500px;
    }
  }

  &:hover {
    color: rgb(255, 255, 255);
  }

  &:focus-visible {
    outline: 2px solid @focus-color;
    outline-offset: -62px;
  }

  &:active {
    opacity: 0.5;
  }
}
