    width: 100%;
  }
}
@media (min-width: 1200px) {
  .page__container {
    width: 1200px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  }
}
.main-container {
  text-align: center;
  flex-grow: 1;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  width: 100%;
  min-height: 57px;
  position: relative;
}
.header__logo {
  display: block;
  position: absolute;
  z-index: 1;
  width: 185px;
  height: 96px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.header__logo:hover {
  opacity: 0.8;
}
.header__logo:focus-visible {
  outline: 2px solid #68a2ca;
  outline-offset: 2px;
}
.header__logo:active {
  opacity: 0.9;
}
.header__logo-image {
  width: 185px;
  height: 96px;
}
.nav {
  position: relative;
}
.nav__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .nav__wrapper {
    flex-direction: row;
    justify-content: space-between;
    position: relative;
  }
}
.nav__list {
  position: relative;
  z-index: 2;
}
@media (min-width: 768px) {
  .nav__list {
    z-index: 0;
  }
}
.nav__toggle {
  color: #ffffff;
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 62px;
  height: 54px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  cursor: pointer;
}
.nav__toggle:hover {
  opacity: 0.6;
}
.nav__toggle:focus-visible {
  outline: 2px solid #333333;
  outline-offset: -2px;
}
.nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__toggle.nav__toggle {
    display: none;
  }
}
.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 20px;
  width: 22px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  z-index: 4;
}
.nav--closed .nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}
.nav--opened .nav__toggle {
  top: 2px;
  left: auto;
  right: 0;
}
.nav--opened .nav__toggle::before,
.nav--opened .nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.nav--opened .nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.nav--opened .nav__toggle::after {
  transform: rotate(-45deg);
}
.nav--opened .nav__toggle:active::before,
.nav--opened .nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
  .nav--closed .site-list__item {
    display: none;
  }
  .nav--closed .user-list__item {
    display: none;
  }
  .nav--nojs .nav__toggle {
    display: none;
  }
  .nav__wrapper::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 7px;
    bottom: 0;
    left: 0;
    z-index: 5;
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.2));
  }
}
