.form__control-label {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  padding: 0;
  margin: 0;
  position: relative;
}
.form__control-input[type="radio"] + .form__control-label {
  cursor: pointer;
  display: block;
  padding-left: 62px;
}
.form__control-input[type="radio"] + .form__control-label::before {
  position: absolute;
  content: "";
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: #f2f2f2;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.form__control-input[type="radio"]:checked + .form__control-label::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #333333;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
}
.form__control-input[type="checkbox"] ~ .form__control-label {
  cursor: pointer;
  display: block;
  padding-left: 62px;
}
.form__control-input[type="checkbox"] ~ .form__control-label::before {
  position: absolute;
  content: "";
  width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: #f2f2f2;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.form__control-input[type="checkbox"]:checked ~ .form__control-label::after {
  position: absolute;
  content: "";
  width: 22px;
  height: 22px;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../img/svg/icon-form-tick.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.form__item:hover .form__control-label::before {
  background-color: #e5e5e5;
}
.form__control-input:focus-visible ~ .form__control-label::before {
  outline: 2px solid #68a2ca;
}
.form__item:active .form__control-label::before {
  background-color: #d5d5d5;
}
.form__item:active .form__control-label::after {
  opacity: 0.3;
}
