.guest-book__header {
  padding-top: 52px;
  margin-bottom: 36px;

  @media (min-width: @tablet-width) {
    padding-top: 95px;
    margin-bottom: 59px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 101px;
    margin-bottom: 111px;
  }
}

.guest-book__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  padding: 0 @base-indent;
  margin: 0 0 24px 0;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 36px;
    margin: 0 0 15px 0;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 25px 0;
  }
}

.guest-book__subtitle {
  text-transform: uppercase;
  padding: 0 @base-indent;
  margin: 0 0 52px 0;

  @media (max-width: @mobile-width) {
    br {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    margin: 0 0 107px 0;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 68px 0;
  }
}
