.header-inner {
  width: 100%;
  background-color: @base-background-color;
  background-image: url("../img/svg/hero-divider.svg"), url("../img/hero-background-mobile.jpg");
  background-repeat: no-repeat, no-repeat;
  background-size: auto cover;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/svg/hero-divider.svg"), url("../img/hero-background-mobile@2x.jpg");
  }

  @media (min-width: @tablet-width) {
    background-image: url("../img/svg/hero-divider.svg"), url("../img/hero-background-desktop.jpg");
    background-position: center bottom, center bottom;
    background-size: auto, cover;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/svg/hero-divider.svg"), url("../img/hero-background-desktop@2x.jpg");
    }
  }

  &--big {
    height: 389px;
    background-position: center bottom, center;

    @media (min-width: @tablet-width) {
      height: 518px;
    }
  }

  &--small {
    height: 158px;
    background-position: center bottom, bottom;

    @media (min-width: @tablet-width) {
      height: 290px;
    }

    @media (min-width: @desktop-width) {
      height: 300px;
    }
  }
}

.header-inner__img {
  display: block;

  &--big {
    width: 282px;

    @media (min-width: @tablet-width) {
      width: 461px;
    }
  }

  &--small {
    width: 229px;

    @media (min-width: @tablet-width) {
      width: 457px;
    }
  }
}
