.social {
  .list-reset();
  display: grid;
  grid-template-columns: repeat(3, min-content);
  height: min-content;
  justify-content: center;
  gap: 6px;
}

.social__item {
  min-width: 48px;
  height: 48px;
  background-color: @base-background-color;
}

.social__link {
  width: 100%;
  min-height: 100%;
  color: #ffffff;

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
