.form {
  width: 280px;
  margin: 0 auto;
  text-align: left;

  @media (min-width: @tablet-width) {
    width: 648px;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
  }
}

.form__group {
  border: none;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0;
  position: relative;
  margin: 0 auto 48px auto;

  @media (min-width: @desktop-width) {
    margin-bottom: @form-indent-desktop;
  }

  &--last {
    margin-bottom: 58px;

    @media (min-width: @tablet-width) {
      margin-bottom: 50px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 48px;
    }
  }
}

.form__group::before {
  @media (max-width: @mobile-width) {
    width: 320px;
    height: 1px;
    background-color: #e5e5e5;
    content: "";
    position: absolute;
    top: -72px;
    left: -20px;
  }
}

.form__title {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
  padding: 0;
  margin: 0 0 20px 0;
  position: relative;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  @media (max-width: @mobile-width) {
    br {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    font-size: 21px;
    margin-bottom: 27px;
  }

  &--single {
    @media (min-width: @tablet-width) {
      padding: 0 176px;
    }

    @media (min-width: @desktop-width) {
      padding: 0 265px;
    }
  }

  &--double {
    @media (min-width: @tablet-width) {
      max-width: 295px;
    }

    @media (min-width: @desktop-width) {
      max-width: 430px;
    }
  }

  // разделительные линейки в планшетной и десктопной версиях
  &--single::before {
    @media (min-width: @tablet-width) {
      height: 1px;
      background-color: #dbdbdb;
      content: "";
      position: absolute;
      top: 50%;
      z-index: -2;
      width: 100%;
      left: 0;
    }
  }

  &--single::after {
    @media (min-width: @tablet-width) {
      background-color: #ffffff;
      content: "";
      position: absolute;
      height: 100%;
      top: 0;
      z-index: -1;
      left: 110px;
      right: 110px;
    }

    @media (min-width: @desktop-width) {
      left: 176px;
      right: 176px;
    }
  }
}

.form__label {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
  color: @base-text-color;
  padding: 0 0 0 16px;
  margin: 0;

  @media (min-width: @tablet-width) {
    width: 295px;
  }

  @media (min-width: @desktop-width) {
    width: 430px;
  }
}

.form__field {
  width: 280px;
  height: 58px;
  background-color: @base-form-color;
  border: none;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  padding: 16px;
  margin: 0 0 16px 0;
  position: relative;

  @media (min-width: @tablet-width) {
    width: 295px;
  }

  @media (min-width: @desktop-width) {
    width: 430px;
  }

  &::placeholder {
    color: rgba(0, 0, 0, 0.3);
  }

  &:hover {
    background-color: #e5e5e5;
  }

  &:focus-visible {
    outline: 2px solid @focus-color;
  }

  &:active {
    background-color: #ffffff;
    outline: 4px solid #e5e5e5;
    outline-offset: -4px;
  }
}

// обертка для дополнительной иконки в поле формы
.form__wrapper-field {
  position: relative;

  &--phone::after,
  &--email::after {
    content: "";
    position: absolute;
    z-index: 1;
    background-color: #e6e6e6;
    background-repeat: no-repeat;
    background-position: center center;
    width: 58px;
    height: 58px;
    top: 0;
    right: 0;
  }

  &--phone::after {
    background-image: url("../img/svg/icon-form-phone.svg");
  }

  &--email::after {
    background-image: url("../img/svg/icon-form-email.svg");
  }
}

// Обертка для двух fieldset'ов
.form__wrapper {
  width: 100%;
  justify-content: center;
  position: relative;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, max-content);
    column-gap: 58px;

    // вертикальная линейка-разделитель
    &::after {
      width: 1px;
      //высота линейки равна высоте fieldset'ов минус их нижний отступ
      height: calc(100% - @form-indent-desktop);
      background-color: #d8d8d8;
      content: "";
      position: absolute;
      top: -8px;
      left: 50%;
    }
  }

  @media (min-width: @desktop-width) {
    column-gap: 100px;
  }
}

// Радиокнопки и чекбоксы
.form__list {
  .list-reset();
  display: grid;
  row-gap: 36px;

  &--radio {
    margin: 10px 0;

    @media (min-width: @tablet-width) {
      margin: 10px auto;
      grid-template-columns: repeat(2, max-content);
      column-gap: 50px;
    }

    @media (min-width: @desktop-width) {
      margin: 28px auto;
      grid-template-columns: repeat(3, max-content);
      column-gap: 64px;
    }
  }

  &--checkbox {
    margin: 10px 0;

    @media (min-width: @tablet-width) {
      margin: 14px auto 2px auto;
      grid-template-columns: repeat(2, max-content);
      column-gap: 82px;
    }

    @media (min-width: @desktop-width) {
      margin: 42px auto;
      grid-template-columns: repeat(4, max-content);
      column-gap: 64px;
    }
  }
}

.form__item {
  &--narrow {
    @media (min-width: @tablet-width) {
      max-width: 190px;
    }

    @media (min-width: @desktop-width) {
      max-width: 200px;
    }
  }

  &--medium {
    @media (min-width: @tablet-width) {
      max-width: 250px;
    }

    @media (min-width: @desktop-width) {
      max-width: 280px;
    }
  }
}

.form__control-label {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  padding: 0;
  margin: 0;
  position: relative;
}

.form__control-input[type="radio"] + .form__control-label {
  cursor: pointer;
  display: block;
  padding-left: 62px;
}

.form__control-input[type="radio"] + .form__control-label::before {
  position: absolute;
  content: "";
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: @base-form-color;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.form__control-input[type="radio"]:checked + .form__control-label::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: @base-text-color;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
}

.form__control-input[type="checkbox"] ~ .form__control-label {
  cursor: pointer;
  display: block;
  padding-left: 62px;
}

.form__control-input[type="checkbox"] ~ .form__control-label::before {
  position: absolute;
  content: "";
  width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: @base-form-color;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.form__control-input[type="checkbox"]:checked ~ .form__control-label::after {
  position: absolute;
  content: "";
  width: 22px;
  height: 22px;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../img/svg/icon-form-tick.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

.form__item:hover .form__control-label::before {
  background-color: #e5e5e5;
}

.form__control-input:focus-visible ~ .form__control-label::before {
  outline: 2px solid @focus-color;
}

.form__item:active .form__control-label::before {
  background-color: #d5d5d5;
}

.form__item:active .form__control-label::after {
  opacity: 0.3;
}

// Поле для дополнительного текста
.form__field-additional {
  width: 100%;
  height: 150px;
  background-color: @base-form-color;
  border: none;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  padding: 16px;
  margin: 0 auto;
  resize: none;

  @media (min-width: @tablet-width) {
    height: 160px;
  }

  @media (min-width: @desktop-width) {
    width: 768px;
  }

  &::placeholder {
    color: rgba(0, 0, 0, 0.3);
  }

  &:hover {
    background-color: #e5e5e5;
  }

  &:focus-visible {
    outline: 2px solid @focus-color;
  }
}

// Кнопка отправки формы
.form__button {
  width: 280px;
  min-height: 58px;
  color: #ffffff;
  background-color: @base-background-color;

  @media (min-width: @tablet-width) {
    width: 324px;
  }

  @media (min-width: @desktop-width) {
    width: 384px;
  }

  &:hover {
    background-color: #669ec0;
  }

  &:focus-visible {
    outline: 2px solid @focus-color;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.form__footnote {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 12px;
  line-height: 21px;
  font-weight: 400;
  color: rgba(51, 51, 51, 0.6);
  text-transform: uppercase;
  display: block;
  text-align: right;
  margin: 2px 0 22px 10px;

  @media (min-width: @tablet-width) {
    font-size: 14px;
    color: @base-text-color;
    margin: auto 0;
  }
}

.form__wrapper-button {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0 auto 22px auto;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 72px;
  }

  @media (min-width: @desktop-width) {
    width: 768px;
  }
}
