img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  // для sticky footer
  height: 100%;
}

.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: @base-text-color;
  height: 100%;
  margin: 0;
}

.page__container {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
  //для sticky footer
  min-height: 100%;

  @media (min-width: @tablet-width) {
    // для фикса:
    // width: 768px;
    // для резины:
    width: 100%;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  }
}

.main-container {
  text-align: center;
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
