.header {
  width: 100%;
  min-height: 57px;
  position: relative;
}

.header__logo {
  display: block;
  position: absolute;
  z-index: 1;
  width: 185px;
  height: 96px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  &:hover {
    opacity: 0.8;
  }

  &:focus-visible {
    outline: 2px solid @focus-color;
    outline-offset: 2px;
  }

  &:active {
    opacity: 0.9;
  }
}

.header__logo-image {
  width: 185px;
  height: 96px;
}
