.button-basic {
  font-family: "PT Sans", "Arial", sans-serif;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  &:focus-visible {
    outline: 2px solid @focus-color;
    outline-offset: 2px;
  }
}

.button-basic--type {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}
