.user-list {
  .list-reset();
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 40%;
  }

  @media (min-width: @desktop-width) {
    width: 480px;
  }
}

.user-list__item {
  background-color: @base-background-color;
  width: 100%;
  min-height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);

  @media (min-width: @tablet-width) {
    background-color: #ffffff;
    border: none;
    width: fit-content;
  }
}

.user-list__item:last-child {
  border-bottom: none;
}

.user-list__link {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  padding: 14px 60px;
  width: 100%;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    padding: @base-indent;
  }

  @media (min-width: @desktop-width) {
    font-size: 14px;
    line-height: 21px;
    color: #766357;
  }

  &:hover {
    background-color: #669ec0;

    @media (min-width: @tablet-width) {
      color: #81b3d2;
      background-color: #ffffff;
    }
  }

  &:focus-visible {
    outline: 2px solid #333333;
    outline-offset: -2px;

    @media (min-width: @tablet-width) {
      outline: 2px solid @focus-color;
    }
  }

  &:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);

    @media (min-width: @tablet-width) {
      color: #b3b3b3;
      background-color: #ffffff;
    }
  }
}
