.search-hotel {
  padding: 38px 0 8px 0;

  @media (min-width: @tablet-width) {
    padding: 58px 0 8px 0;
  }
}

.search-hotel__subtitle {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  padding: 0 @base-indent;
  margin: 0 0 20px 0;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 35px;
    margin-bottom: 28px;
  }
}

.search-hotel__description {
  text-transform: uppercase;
  padding: 0 @base-indent;
  margin: 0 0 32px 0;

  @media (min-width: @tablet-width) {
    margin-bottom: 45px;
  }

  @media (max-width: @mobile-width) {
    br {
      display: none;
    }
  }
}

.search-hotel__link {
  width: 284px;
  min-height: 58px;
  text-decoration: none;
  color: #ffffff;
  background-color: @base-background-color;
  padding: 16px;
  margin: 32px auto 49px auto;

  @media (min-width: @tablet-width) {
    width: 458px;
    margin: 32px auto 56px auto;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 66px;
  }

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.search-hotel__map {
  width: 320px;
  height: 350px;
  display: block;
  border: none;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 768px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
  }
}
