.advantages__header {
  padding-top: 90px;
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    padding-top: 76px;
    margin-bottom: 48px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 58px;
  }
}

.advantages__text {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  padding: 0 24px;
  margin: 0 0 39px 0;

  @media (min-width: @tablet-width) {
    font-size: 21px;
    margin: 0 0 30px 0;
  }
}

.advantages__title {
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  padding: 0 @base-indent;
  margin: 0 0 30px 0;

  @media (min-width: @tablet-width) {
    margin: 0 0 54px 0;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 44px 0;
  }
}

.advantages__list {
  .list-reset();
  display: flex;
  flex-direction: column;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    width: 768px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
  }
}

.advantages__item {
  position: relative;

  &--complex {
    color: #ffffff;
    background-color: @base-background-color;
    padding: 0;

    @media (min-width: @tablet-width) {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
    }
  }

  &--order-reverse {
    flex-direction: row-reverse;
  }

  &--simple {
    width: 320px;
    background-color: @secondary-background-color;
    padding: 24px 33px;

    @media (min-width: @tablet-width) {
      width: 256px;
      padding: 41px 33px;
    }

    @media (min-width: @desktop-width) {
      width: 400px;
      padding: 52px 50px;
    }
  }

  @media (max-width: @mobile-width) {
    &:first-child {
      padding-top: @base-indent;
    }

    &:last-child {
      padding-bottom: 33px;
    }

    &:first-child::before {
      width: 320px;
      height: 37px;
      content: url("../img/svg/hero-divider-bottom.svg");
      position: absolute;
      top: -16px;
      left: 50%;
      transform: translateX(-50%);
    }

    &--dividing-line::before {
      width: 284px;
      height: 1px;
      background-color: #d6d6d6;
      content: "";
      position: absolute;
      top: 0;
      left: @base-indent;
    }
  }
}

.advantages__photo {
  display: block;

  @media (min-width: @tablet-width) {
    width: 512px;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
  }
}

.advantages__container {
  width: 320px;
  padding: 37px @base-indent;

  @media (min-width: @tablet-width) {
    width: 256px;
    padding: 52px 30px 30px 30px;
  }

  @media (min-width: @desktop-width) {
    width: 400px;
    padding: 52px 54px;
  }
}

.advantages__subtitle {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: inherit;
  padding: 0;
  margin: 0 0 12px 0;

  @media (max-width: @mobile-width) {
    br {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    font-size: 21px;
  }
}

.advantages__number {
  display: block;
  margin-bottom: 16px;

  @media (min-width: @tablet-width) {
    margin-bottom: 24px;
  }
}

.advantages__description {
  text-transform: uppercase;
  padding: 0;
  margin: 0;

  @media (max-width: @mobile-width) {
    br {
      display: none;
    }
  }
}
