.gallery__header {
  padding-top: 52px;
  margin-bottom: 36px;

  @media (min-width: @tablet-width) {
    padding-top: 95px;
    margin-bottom: 59px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 101px;
    margin-bottom: 94px;
  }
}

.gallery__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  padding: 0 @base-indent;

  @media (max-width: @mobile-width) {
    br {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 36px;
  }

  &--top {
    margin: 0 0 25px 0;

    @media (min-width: @tablet-width) {
      margin-bottom: 26px;
    }
  }

  &--bottom {
    margin: 0 0 28px 0;

    @media (min-width: @tablet-width) {
      margin-bottom: 32px;
    }
  }
}

.gallery__subtitle {
  text-transform: uppercase;
  padding: 0 22px;
  margin: 0 0 38px 0;

  @media (max-width: @mobile-width) {
    br {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    margin: 0 0 82px 0;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 90px 0;
  }
}

.gallery__list {
  .list-reset();
  display: grid;
  margin: 0 20px 15px 20px;

  @media (min-width: @tablet-width) {
    width: 648px;
    margin: 0 auto 52px auto;
    grid-template-columns: repeat(2, 324px);
    gap: 0;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 54px 0;
    grid-template-columns: repeat(3, 400px);
    gap: 0;
  }
}

.gallery__item {
  width: 100%;
  padding: 24px 0 21px 0;
  position: relative;

  // линейки в мобильной версии
  @media (max-width: @mobile-width) {
    &--dividing-line::before,
    &--dividing-line:last-child::after {
      width: 320px;
      height: 1px;
      background-color: #e5e5e5;
      content: "";
      position: absolute;
      left: -20px;
    }

    &--dividing-line::before {
      top: 0;
    }

    &--dividing-line:last-child::after {
      bottom: 0;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 0;
  }

  &--foto-wide {
    @media (min-width: @tablet-width) {
      grid-column: 1 / -1;
    }
  }
}

.gallery__photo {
  width: 100%;
  object-fit: cover;
  display: block;
  margin-bottom: 15px;

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
}

.gallery__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @media (min-width: @tablet-width) {
    text-align: left;
    min-height: 80px;
    background-color: #f2f2f2;
    flex-direction: row;
    justify-content: space-between;
    padding: 22px 14px 5px 20px;
  }

  @media (min-width: @desktop-width) {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    left: 0;
    bottom: 0;

    &:hover {
      background-color: transparent;
    }
  }
}

.gallery__wrapper--accented-background {
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    background-color: #eaeaea;
  }
}

.gallery__inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-height: 54px;

  @media (min-width: @tablet-width) {
    margin-right: 30px;
  }
}

.gallery__signature {
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
  padding: 0;
  margin: 0;

  @media (min-width: @desktop-width) {
    color: #ffffff;
  }
}

.gallery__author {
  padding: 0;
  margin: 0 0 13px 0;

  @media (min-width: @desktop-width) {
    color: #ffffff;
  }
}

.gallery__like-button {
  border: none;
  display: flex;
  align-items: baseline;
  justify-content: center;
  color: rgba(0, 0, 0, 0.3);
  background-color: transparent;
  min-width: 68px;
  min-height: 30px;
  cursor: pointer;
  margin-left: -4px;

  @media (min-width: @tablet-width) {
    margin-top: 5px;
  }

  @media (min-width: @desktop-width) {
    color: #ffffff;
  }

  &:hover {
    color: @focus-color;

    @media (min-width: @desktop-width) {
      background-color: rgba(0, 0, 0, 0.3);
    }
  }

  &:focus-visible {
    outline: 2px solid @focus-color;
  }

  &:active {
    opacity: 0.8;
  }
}

.gallery__rating {
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  color: #000000;
  display: block;
  margin-left: 10px;
  width: 100%;

  @media (min-width: @desktop-width) {
    color: #ffffff;
  }
}

.gallery__rating-icon {
  width: 100%;
}

.gallery__video-container {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 648px;
    margin-bottom: 48px;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
    margin-bottom: 90px;
  }
}
