.page-footer {
  display: flex;
  flex-direction: column;
  margin: 33px auto;
  align-items: center;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    width: 648px;
    margin: 28px auto;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
  }
}

.page-footer__link {
  color: #000000;
  text-decoration: none;
  display: grid;
  gap: 12px;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    justify-content: left;
    margin-right: 30px;
    margin-bottom: 0;
  }

  &--last {
    margin-top: 30px;
    grid-template-columns: min-content auto;

    @media (min-width: @tablet-width) {
      margin-top: 0;
      margin-left: 30px;
      margin-right: 0;
      justify-content: right;
    }
  }

  &:hover {
    color: #81b3d2;
  }

  &:focus-visible {
    outline: 2px solid @focus-color;
    outline-offset: 2px;
  }

  &:active {
    color: #b3b3b3;
  }
}

.page-footer__text {
  text-transform: uppercase;
  margin: 0;

  &--hashtag {
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    padding: 0 @base-indent;

    @media (min-width: @tablet-width) {
      padding: 0;
    }
  }

  &--made-in {
    padding: 0;
    width: 100%;
  }
}
